
.navbar {
    background-color: #282c34; 
    padding: 10px; 
    display: flex; 
    justify-content: flex-start; 
  }
  
  .navbar a {
    color: white; 
    text-decoration: none; 
    margin-right: 30px; 
  }
  
  .navbar a:last-child {
    margin-right: 0; 
  }
  
  .navbar a:hover {
    text-decoration: underline; 
  }
  